<script>
import api from "@/api"
import Input from "@/components/Input";
import Select from "@/components/Select";
import Contacts from "@/components/Contacts";
import Cases from "@/components/Cases/index"
import Masters from "@/components/Masters/index"
import Reviews from "@/components/Reviews/index"
import Brands from "@/components/Brands";
import ProductCard from "@/components/ProductCard";
import Loader from "@/components/Loader";
import VueSlickCarousel from 'vue-slick-carousel'
import 'vue-slick-carousel/dist/vue-slick-carousel.css'

export default {
  name: "Home",
  components: {Input, Select, VueSlickCarousel, Contacts, Cases, Masters, Reviews, Brands, ProductCard, Loader},
  created() {
    this.getProducts()
  },
  mounted() {
    this.$nextTick(function () {
      if (this.$route.hash === '#form') {
        setTimeout(() => this.goto('form'), 300)
      }
    })
  },
  computed: {
    isValid() {
      return !!this.form.phone
    }
  },
  methods: {
    goto(refName) {
      const top = this.$refs[refName].offsetTop;
      window.scrollTo(0, top);
    },
    nextProduct() {
      this.$refs.products.next()
    },
    prevProduct() {
      this.$refs.products.prev()
    },
    async getProducts() {
      this.products = await api.getHomeProducts()
    },
    async formSubmit() {
      this.formLoading = true
      await api.submitForm(this.form)
      await this.$store.dispatch('createNotification', 'Заявка отправлена. Мы свяжемся с вами в ближайшее время')
      this.formLoading = false
    }
  },
  data: () => ({
    formLoading: false,
    products: [],
    productsSetting: {
      slidesToShow: 3,
      arrows: false,
      dots: false,
      touchMove: false,
      variableWidth: true,
      adaptiveHeight: true,
      responsive: [
        {
          breakpoint: 1170,
          settings: {
            slidesToShow: 1,
            swipe: true,
            touchMove: true,
            draggable: true,
            centerMode: true
          }
        }
      ]
    },
    form: {
      service: 'Окрашивание волос',
      name: '',
      phone: ''
    },
    services: ['Окрашивание волос', 'Стрижка', 'Маникюр']
  })
}
</script>

<style scoped>
.form-loading {
  position: relative;
}

.loader {
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  background-color: rgba(0, 0, 0, .2);
  z-index: 10;
}
</style>