<template>
  <div class="product-card">
    <div class="price">{{ product.price }} руб.</div>
    <div class="img">
      <img :src="getSrc(product.thumb)" alt="">
    </div>
    <div class="title">{{ product.name }}</div>
    <div class="text">{{ product.text }}</div>
    <div
        v-if="!isInBasket"
        @click="addToCart"
        :class="[
            'flex-between align-center card-link',
            {'item-loading': loading}
        ]"
    >
      <span>Купить</span>
      <svg width="12" height="10" viewBox="0 0 12 10" fill="none" xmlns="http://www.w3.org/2000/svg">
        <path d="M11.1665 4.81706L1.1665 4.81706" stroke="#22252C" stroke-width="1.5" stroke-linecap="round"
              stroke-linejoin="round"/>
        <path d="M7.1333 0.800829L11.1666 4.81683L7.1333 8.8335" stroke="#22252C" stroke-width="1.5"
              stroke-linecap="round" stroke-linejoin="round"/>
      </svg>
      <Loader class="loader" v-if="loading"></Loader>
    </div>
    <div v-if="isInBasket" class="flex-center align-center card-link">
      <QuantityInput class="quantity-input" @input="handleQuantityChange" v-model="getProductInBasket.quantity"></QuantityInput>
    </div>
  </div>
</template>

<script>
import _ from 'lodash'
import {createImageUrl} from "@/utils";
import Loader from "@/components/Loader";
import QuantityInput from "@/components/QuantityInput";

export default {
  name: "ProductCard",
  props: ['product'],
  components: {QuantityInput, Loader},
  data: () => ({
    loading: false
  }),
  computed: {
    isInBasket() {
      return this.$store.getters.basket.some(item => item.product.id === this.product.id)
    },
    getProductInBasket() {
      return this.$store.getters.basket.find(item => item.product.id === this.product.id)
    }
  },
  methods: {
    handleQuantityChange: _.debounce(function(quantity) {
      this.$store.dispatch('changeProductQuantity', {
        productId: this.getProductInBasket.id,
        quantity
      })
    }, 300),
    getSrc(src) {
      return createImageUrl(src)
    },
    async addToCart() {
      this.loading = true
      await this.$store.dispatch('addProductToBasket', this.product)
      this.loading = false
      await this.$store.dispatch('createNotification', 'Товар добавлен в корзину')
    }
  }
}
</script>

<style scoped>
.card-link {
  cursor: pointer;
}
.product-card:hover .quantity-input {
  color: #fff;
}
.item-loading {
  color: #abacb1 !important;
}
.loader {
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  background-color: rgba(0, 0, 0, .2);
  z-index: 10;
  min-height: 0;
}
</style>