<template>
  <div class="input-wrap">
    <div class="label">{{ label }}</div>
    <div
        v-click-outside="hide"
        :class="[
            'select',
            {'select-disabled': disabled}
        ]"
        @click="!disabled ? open = !open : false"
    >
      <svg width="12" height="8" viewBox="0 0 12 8" fill="none" xmlns="http://www.w3.org/2000/svg">
<path d="M10.6668 1.6665L6.00016 6.33317L1.3335 1.6665" stroke="#200E32" stroke-width="1.5" stroke-linecap="round" stroke-linejoin="round"/>
</svg>
      <div class="current">{{value}}</div>
      <div class="dropdown" v-if="open">
        <div
            class="option"
            v-for="(option, i) in options"
            :key="i"
            @click="selectOption(option)"
        >{{option}}</div>
      </div>
    </div>
  </div>
</template>

<script>
import ClickOutside from 'vue-click-outside'
export default {
  name: "Select",
  props: ['label', 'value', 'options', 'disabled'],
  data: () => ({
    open: false
  }),
  methods: {
    selectOption(option) {
      this.$emit('input', option);
    },
    hide() {
      this.open = false
    }
  },
  directives: {
    ClickOutside
  }
}
</script>

<style scoped>
.select-disabled {
  cursor: not-allowed !important;
}
</style>